        width: 94%;
        height: 82%;
        // border: solid 1px #11c2ff;
        // background: rgba(17, 194, 255, 0.1);
        background: rgba(0, 0, 0, 0.45);
        color: #fff;
        font-size: 16px;

        ul li {
          padding: 0px 0px 0px 16px;
          height: 30px;
          line-height: 30px;
          margin: 6px 0 0 0px;

          span {
            // width: 215px !important;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
          }
        }

        .dizhen {
          width: 100%;
          // height: 56%;
          margin: 10px 0 0 16px;

          .addmech {
            float: left;
            margin-left: 130px;
            margin-top: -7px;
            background: rgba(3, 47, 142, 0.5);
            color: #00F1E5;
            line-height: 24px;
            text-align: center;
            cursor: pointer;
          }

          .top {
            width: 48px;
            height: 24px;
            background: rgba(3, 47, 142, 0.5);
            color: #00F1E5;
            line-height: 24px;
            text-align: center;
            float: right;
            margin-right: 21px;
            margin-top: -5px;
            cursor: pointer;
          }

          .top:hover {
            color: #fff;
          }
        }
      }
    }

    // 匹配预案
    .ppya {
      width: 100%;
      height: 18%;
      margin: 10px 0px 0px 4px;

      .content {
        width: 94%;
        height: 82%;
        // border: solid 1px #11c2ff;
        // background: rgba(17, 194, 255, 0.1);
        // color: #fff;
        // font-size: 16px;
        margin: 32px 0 0 0px;
      }
    }

    // 先期处置信息
    .xqczinfo {
      width: 97%;
      height: 50%;
      margin: 0 0 0 14px;

      .content {
        width: 96%;
        height: 60%;
        margin: -6px 0 0 10px;
        color: #FFFFFF;
        overflow-y: scroll;

        ul li {
          padding: 0px 0px 0px 16px;
          border: 1px solid #1D7295;
          width: 283px;
          height: 34px;
          line-height: 34px;
          margin: 6px 0 0 0px;
          background-color: rgba(6, 44, 65, 0.5);
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          cursor: pointer;
        }

        li:hover {
          background: rgba(158, 131, 36, 0.5);
          color: #FFCB1E;
          border: 1px solid #9E8324;
        }

        .ulyellow {
          background: rgba(158, 131, 36, 0.5);
          color: #fff;
          border: 1px solid #9E8324;
        }

        .ulgreen {
          background: rgba(12, 168, 107, 0.5);
          color: #fff;
          border: 1px solid #08A467;
        }

        .ulblue {
          background: rgba(44, 133, 164, 0.5);
          color: #fff;
          border: 1px solid #2C85A4;
        }
      }
    }
  }

  #main {
    flex: 1;
    position: relative;

    // 流程
    .process {
      display: flex;
      height: 20%;
      width: 100%;
      position: absolute;
      top: 76%;
      left: 4%;
      text-align: center;
      pointer-events: auto;

      .box {
        width: 232px;
        height: 104px;
        background: url('../../assets/images/home/bottom_blue_bj.png') no-repeat;
        color: #3AAAA4;
        cursor: pointer;

        label {
          display: block;
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background: #f00;
          margin: 30px 0 0 -4px;
        }

        span {
          font-size: 24px;
          line-height: 64px;
          font-family: 'yang-myfont';
          // font-style: oblique;
        }
      }

      .active {
        color: #ff5534;
        background: url('../../assets/images/home/bottom_juse_bj.png') no-repeat;
      }
    }

    // 时间组件
    .event-time {
      position: absolute;
      top: 2%;
      left: 6%;
    }

    // 地图组件
    .map {
      position: absolute;
      top: 4%;
      left: 51%;
      pointer-events: auto;
    }
  }

  #right {
    flex: 0 0 380px;
    pointer-events: auto;

    // background: red;
    // 指挥调度
    .command {
      width: 100%;
      margin: 30px 0px 0px -10px;
    }

    // 已调度资源
    .yddresour {
      height: 32%;
      position: relative;
      margin: 16px 0 0 0px;

      .btn-addres {
        width: 100px;
        height: 30px;
        background: rgba(3, 47, 142, 0.5);
        color: #00f1e5;
        line-height: 30px;
        text-align: center;
        position: absolute;
        top: 0%;
        left: 68%;
        cursor: pointer;
      }

      .content {
        width: 85%;
        height: 86%;
        color: #fff;
        font-size: 16px;
        margin: 0 0 0 10%;
        pointer-events: auto;
      }
    }
  }
}
</style>



