<template>
  <div class="box1" style="width: 100%">
    <el-table
      :data="tableData"
      :cell-style="cellstyle"
      :row-style="rowstyle"
      :header-cell-style="headerstyle"
      :header-row-style="headerstyle"
      style="width: 100%; background-color: transparent !important;"
    >
      <el-table-column prop="unit" label="呈报单位" width="120"></el-table-column>
      <el-table-column prop="assignee" label="受理人" width="120"></el-table-column>
      <el-table-column prop="content" label="报告内容" width="150"></el-table-column>
      <el-table-column prop="status" label="状态" width="100"></el-table-column>
      <el-table-column prop="date" label="上报时间"></el-table-column>
    </el-table>
    <el-input
      class="mytextarea"
      type="textarea"
      :rows="6"
      placeholder="批示内容"
      resize="none"
      v-model="textarea"
    ></el-input>
    <el-input
      class="mytextarea"
      type="textarea"
      :rows="1"
      placeholder="批示人"
      resize="none"
      v-model="textarea"
    ></el-input>
    <div class="btn-group">
      <el-button size="medium" type="success">确定</el-button>
      <!-- <el-button size="medium" type="primary">取消</el-button> -->
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // mytime: new Date(),
      tableData: [{
        unit: '应急指挥中心',
        assignee: '值班员01',
        content: '应急人员到达现场',
        status: '已签收',
        date: '2019/11/12 20:50'
      }],
      textarea: ''
    }
  },
  methods: {
    rowstyle () {
      return 'background-color: transparent; border: 0; color: #88d6ff;text-align: center;'
    },
    cellstyle ({ row, column, rowIndex, columnIndex }) {
      if (column.property === 'status') {
        switch (row.status) {
          case '已签收': return { backgroundColor: 'transparent', textAlign: 'center', color: '#67C23A', border: 0 }
            break
          case '等签收': return { background: '#F56C6C', color: '#FFF', border: 0 }
            break
          default: return 'background-color: transparent; border: 0; color: #88d6ff;text-align: center;'
        }
      }
      return 'background-color: transparent; border: 0; color: #88d6ff;text-align: center;'
    },
    headerstyle () {
      return 'background-color: transparent; border: 0; color: #00f1e5; text-align: center; font-size: 16px;'
    },
    gettime () {
      return new Date()
    }
  }
}
</script>

<style lang="stylus">
.has-gutter {
  // background : transparent !important;
  text-align: center;
}

.el-table::before {
  height: 0;
}

.mytextarea {
  box-sizing: border-box;
  margin-top: 15px;
  border-radius: 4px;
  background: rgba(255, 255, 255, 0.1);
}

.mytextarea textarea {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #02e1e8;
  color: #fff;
}

.btn-group {
  text-align: center;
  margin-top: 20px;
}
</style>



