<!-- 匹配预案列表-->
<template>
  <div class="items">
    <div class="content4" @click="getMore()">
      <v-title title="匹配预案"></v-title>
      <el-table
        :data="tableData"
        :row-style="rowstyle"
        :cell-style="cellstyle"
        :header-cell-style="headercellstyle"
        :header-row-style="headerrowstyle"
        style="width: 100%; background-color: transparent;"
      >
        <el-table-column prop="shName" label="预案名称" width="110">
          <template slot-scope="scope">
            <div style="cursor:pointer;" @click="getMore(scope.row)">{{ scope.row.shName}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="shEventType" label="预案类型" width="110"></el-table-column>
        <el-table-column prop="handle" label="状态">
          <template slot-scope="scope">
            <div
              style="cursor:pointer;"
              @click="startScheme(scope.row.shId, scope.row.shName)"
            >{{ scope.row.handle}}</div>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
import Title from '../../components/Title'
import ResourcesTab from '../../components/ResourcesTab'
// import { reportedEvent } from '../../api/visual'
// import { getSchemeList } from '../../api/scheme'
// import { saveObject, postEvent} from '../../api/event'
import { builderParams } from '@/utils/builderParams'
import { post, get } from '@/utils/request'
export default {
  data () {
    return {
      tableData: []
    }
  },
  props: {
    ajlb: {
      type: String,
      default: ''
    },
    eventId: {
      type: String,
      default: ''
    }
  },
  components: {
    'v-title': Title
  },
  methods: {
    getuuid () {
      return ((((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + '' + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1) + (((1 + Math.random()) * 0x10000) | 0).toString(16).substring(1))
    },
    getNowTime () {
      return new Date(+new Date() + 8 * 3600 * 1000).toISOString().replace(/T/g, ' ').replace(/\.[\d]{3}Z/, '')
    },
    async searchScheme (eventType) {
      if (eventType.indexOf('事故灾难') >= 0) {
        eventType = 'Accident_Disaster'
      }
      if (eventType.indexOf('社会安全') >= 0) {
        eventType = 'Social_Security'
      }
      if (eventType.indexOf('公共卫生') >= 0) {
        eventType = 'Public_Health'
      }
      if (eventType.indexOf('自然灾害') >= 0) {
        eventType = 'Natural_Disaster'
      }
      var table = {
        page: 1,
        pageSize: 3,
        // name: '',
        // type: '',
        eventType: eventType
      }
      const configuration = await get({ url: 'static/config.json', data: {} })
      const getSchemeList = configuration.scheme.baseUrl + configuration.scheme.getSchemeList
      var result = await get({ url: getSchemeList, data: table })
      if (result.context.status === 200) {
        if (result.data.totalItems > 0) {
          result.data.resultList.forEach(item => {
            let typeName = ''
            if (item.shType === 'Common') {
              typeName = '总体应急预案'
            } else if (item.shType === 'Special') {
              typeName = '专项应急预案'
            } else if (item.shType === 'Organization') {
              typeName = '部门应急预案'
            } else if (item.shType === 'Basic') {
              typeName = '基层应急预案'
            } else if (item.shType === 'Unit') {
              typeName = '单位应急预案'
            } else if (item.shType === 'Own') {
              typeName = '自定义预案'
            }
            this.tableData.push({
              shId: item.id,
              shName: item.shName,
              shEventType: typeName,
              // 已启动||未启动
              handle: '未启动'
            })
          })
        }
      }
      // 查询预案启动状态
      var schemeConditions = [{ Property: 'eventId', Oper: 'eq', Value: this.eventId }]
      let schemeItme = builderParams.get('SCHEME_RESPONSE', '', schemeConditions)
      const eventScheme = await post({ url: builderParams.getUrl(), data: schemeItme })

      if (eventScheme.Result === 0) {
        eventScheme.Data.forEach(item => {
          for (let index in this.tableData) {
            if (item.Schemeid === this.tableData[index].shId) {
              this.tableData[index].handle = '已启动'
            }
          }
        })
      }
    },
    updateState (shId) {
      for (let index in this.tableData) {
        if (shId === this.tableData[index].shId) {
          this.tableData[index].handle = '已启动'
        }
      }
    },
    async startScheme (shId, shName) {
      // // 到指挥调度页面
      // var table = {
      //   DataBase: 'yjzh',
      //   DynamicTable: {
      //     Conditions: null,
      //     TableData: {
      //       id: this.getuuid(),
      //       schemeId: shId,
      //       title: '',
      //       startTime: this.getNowTime(),
      //       type: 0,
      //       createunitname: '测试单位',
      //       createTime: this.getNowTime(),
      //       eventId: this.eventId
      //     },
      //     TableName: 'Scheme_response',
      //     WorkType: 'Save'
      //   }
      // }
      // const result = await saveObject(table)
      // // 请求可视化系统对外接口 投大屏
      //  var task = {
      //   DataBase: 'duty',
      //   DynamicTable: {
      //     Conditions: null,
      //     TableData: {
      //       // 编号
      //       bh: this.getuuid(),
      //       // 系统名称
      //       xtmc: 'zhdd',
      //       //调用地址
      //       jkdz: reportedEvent,
      //       // 调用类型
      //       ajaxType: 'POST',
      //       // 请求参数
      //       param: '',
      //       // 状态
      //       state: 0,
      //       // 入库时间戳
      //       rksjc: this.getNowTime(),
      //       // 更新时间
      //       gxsj: null,
      //       // 操作人编号
      //       gxrbh: '',
      //       // 操作人
      //       gxr: ''
      //     },
      //     TableName: 'quartz',
      //     WorkType: 'Save'
      //   }
      // }
      // var param = {eventId: this.eventId, planId: shId, name: shName}
      // task.DynamicTable.TableData.param = JSON.stringify(param)
      // const taskResult = await saveObject(task)
      // if (result.Result === 0) {
      //   alert('启动成功！')
      //   this.$router.push('/command/?eventId=' + this.eventId)
      // }
    },
    rowstyle () {
      return 'background-color: rgba(17,194,255,0.1); color: #88d6ff;text-align: center;'
    },
    cellstyle () {
      return 'background: rgba(17,194,255,0.1); text-align: center; color: #00f1e5;'
    },
    headercellstyle () {
      return 'background-color: rgba(17,194,255,0.1); text-align: center;'
    },
    headerrowstyle () {
      return 'background-color: rgba(17,194,255,0.1); text-align: center; color: #fff; font-size: 16px border-bottom: 1px solid white'
    },
    getMore (index) {
      // alert('预案名称：' + index.shName)
      let id = this.$layer.iframe({
        content: {
          content: ResourcesTab, // 传递的组件对象
          parent: this, // 当前的vue对象
          data: { shName: index.shName, shId: index.shId, handle: index.handle }// props
        },
        shade: false, // 是否显示遮罩
        area: ['700px', '600px'],
        offset: ['900', '520'],
        title: '预案匹配资源'
      })
      this.layerId = id
    }
  },
  watch: {
    ajlb: function (newval, oldval) {
      if (newval) {
        this.searchScheme(newval)
      }
    },
    eventId: function (newval, oldval) {
    }
  },
  mounted () {
  }
}
</script>

<style lang="stylus">
.items {
  width: 310px;

  .content4 {
    width: 310px;
    margin: 0 0 0 2px;
  }
}

.el-table--fit {
  // border: solid 1px #11c2ff;
  width: 110%;
  background: rgba(17, 194, 255, 0.1);
}

.el-table td div {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
</style>



